html {
  font-family: Roboto, sans-serif;
}

body {
  box-sizing: border-box;
  background-color: silver;
  margin: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 16px;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  margin-right: auto;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews-numbers {
  margin: 0;
}

.card__reviews {
  text-align: right;
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin-top: 16px;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.stars {
  flex-direction: row;
  width: 96px;
  height: auto;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 > .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__price {
  justify-content: space-between;
  width: 166px;
  height: 18px;
  display: flex;
}

.card__price-title {
  text-align: left;
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}
